<template>
  <div class="mt-12">
    <div class="container">
      <p class="display-2 font-weight-bold">
				Harga
			</p>

      <v-row>
        <v-col 
          v-for="item in paket" :key="item.img"
          cols="12" md="6">
          <v-card flat>
            <v-img :src="require('@/assets/paket/'+item.img)"></v-img>
            <v-card-text>
              <div class="title">{{ item.title }}</div>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                depressed
                @click="pesan(item.id)"
                color="primary lighten-3">
                pesan
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    paket: [
      { img: 'FREE.png', title: 'Inkubator dapat memenejemen maksimal 4 startup', id: 1 },
      { img: 'BASIC.png', title: 'Inkubator memiliki layanan penuh dari fitur progresio dengan maksimal 15 startup aktif', id: 2 },
      { img: 'PLATINUM.png', title: 'Inkubator memiliki layanan penuh dari fitur progresio dengan maksimal 50 startup aktif', id: 3 },
      { img: 'UNLIMITED.png', title: 'Inkubator memiliki layanan penuh dari fitur progresio dengan tanpa batas startup aktif', id: 4 }
    ]
  }),

  methods: {
    pesan(val) {
      window.localStorage.setItem('id_paket', val)
      this.$router.push({ name: 'register-incubator' })      
    }
  }
}
</script>

