import { 
  postDataAccept,
  getDataNotif
} from "@/config/incubator/notif.js";

const dataNotif = {
  namespaced: true,
	state: {
		dataNotif: [],
		isLoading: false,
		status: null
	},
	mutations: {
		updateDataNotif(state, payload) {
			state.dataNotif = payload
		},
		updateStatus(state, payload) {
			state.status = payload
		},
		updateIsLoading(state, payload) {
			state.isLoading = payload
		}
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataNotif(context) {
			getDataNotif()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataNotif', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataAccept(context, payload) {
			postDataAccept(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataNotif')
						if (res.data.status === 'warning') {
              context.commit('updateStatus', 'warning')
            } else {
              context.commit('updateStatus', true)
            }
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
	}
}

export default dataNotif