import {
	getDataAward,
	postDataAward,
	putDataAward,
	deleteDataAward
} from '@/config/incubator/award'

const dataAward = {
	namespaced: true,
	state: {
		dataAward: [],
		isLoading: false,
		status: null
	},
	mutations: {
		updateDataAward(state, payload) {
			state.dataAward = payload
		},
		updateStatus(state, payload) {
			state.status = payload
		},
		updateIsLoading(state, payload) {
			state.isLoading = payload
		}
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataAward(context) {
			getDataAward()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataAward', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataAward(context, payload) {
			postDataAward(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataAward')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataAward(context, payload) {
			// console.log(payload.id)
			const formdata = payload
			putDataAward(formdata.get('id'), payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataAward')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataAward(context, payload) {
			deleteDataAward(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataAward')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataAward