import {
	postRegisterIncubator,
	postRegisterStartup
} from '@/config/register'
import router from '@/router/index'

const dataRegister = {
	namespaced: true,
	state: {
		status: null,
		isLoading: false
	},
	mutations: {
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		},
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		postRegisterStartup(context, payload) {
			postRegisterStartup(payload)
				.then(res => {
					if (res.status === 200) {
						// window.localStorage.setItem('token_abp', res.data.token)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
						setTimeout(() => router.push({ name: 'login-startup' }), 2000)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postRegisterIncubator(context, payload) {
			postRegisterIncubator(payload)
				.then(res => {
					if (res.status === 200) {
						// window.localStorage.setItem('token_abp', res.data.token)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
						setTimeout(() => router.push({ name: 'login-incubator' }), 2000)						
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataRegister