import http from '../http'

export function getDataMentor() {
	return http.get('incubator/mentor')
}

export function postDataMentor(data) {
	return http.post('incubator/mentor', data)
}

export function putDataMentor(id, data) {
	return http.post('incubator/mentor/update/'+id, data)
}

export function deleteDataMentor(data) {
	return http.delete('incubator/mentor/'+data)
}