<template>
  <v-navigation-drawer 
    v-model="drawer"
    fixed
    temporary
    :permanent="false">
    <v-list
      dense
      nav>
      <v-list-item-group v-model="list" color="purple">
        <template
          v-for="item in nav">
          <v-list-item
            :key="item.text"
            link>
            <v-list-item-content>
              <v-list-item-title>
                <a :href="item.link">{{ item.text }}</a>
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item> 
        </template>
        <v-list-item link>
            <v-list-item-content>
              <v-list-item-title>
                <v-btn 
                  color="purple" dark
                  depressed small
                  @click="$router.push({name: 'login-incubator'})">
                  Login
                </v-btn>
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item> 
      </v-list-item-group>      
    </v-list>
  </v-navigation-drawer>	
</template>

<style scoped>
  a{
    color: #A438B6 !important;
    text-decoration: none;
  }
</style>

<script>
	export default {
		props: ['drawer'],

		data: () => ({
			list: 0,
			nav: [
        { text: 'About Us', link: '#aboutus' },
        { text: 'Execellence', link: '#excellence' },
        { text: 'Features', link: '#features' },
        { text: 'Testimonial', link: '#testimonial' },
        { text: 'Contact Us', link: '#contactus' },
        { text: 'Sign Up', link: 'register-incubator' },
      ]
		}),

    watch: {
      drawer(val) {
        if (val === false) {
          this.$emit('drawer', false)
        }
      }
    },

    methods: {
      keluar () {
        window.localStorage.removeItem('token_sekolah')
        this.$router.push({name: 'login'})
      }
    }
	}
</script>