<template>
	<div class="fifth mt-lg-12" id="excellence">
		<div class="container">
			<p class="display-2 font-weight-bold">
				Keunggulan Produk Kami
			</p>
			<v-card flat color="blue lighten-5">
				<v-card-title>
					<div class="purple--text">
						Berbagai keunggulan di dalam satu aplikasi						
					</div>
					<v-spacer></v-spacer>
					<v-btn
						fab depressed
						small
						class="mr-3 white--text purple-abp"
						:disabled="clicked < data.length-3?false:true"
						@click="changeSlide('left')">
						<v-icon>mdi-arrow-left</v-icon>
					</v-btn>
					<v-btn
						fab depressed
						small
						:disabled="clicked > 0?false:true"
						class="white--text purple-abp"
						@click="changeSlide('right')">
						<v-icon>mdi-arrow-right</v-icon>
					</v-btn>
				</v-card-title>
				<v-card-text style="overflow: hidden;">
					<v-row
						:style="{
							'transform': `translateX(${geser}%)`,
							'transition': 'transform 0.3s ease',
							'flex-wrap': 'nowrap'
						}">
							<v-col 
								cols="12" md="4" sm="12"
								v-for="(item, i) in data" :key="i">
								<v-card flat>
									<div 
										style="height: 200px;" 
										class="purple-abp d-flex justify-center align-center">
										<v-img
											:src="require(`../../assets/advantages/${item.img}`)"
											max-width="50%"
											alt="advantages"
										></v-img>
									</div>
									<v-card-text>
										<div class="headline black--text font-weight-bold">
											{{ item.title }}
										</div>
										<div>{{ item.desc }}</div>
									</v-card-text>
								</v-card>
							</v-col>						
					</v-row>
				</v-card-text>
			</v-card>
		</div>
	</div>
</template>

<script>
	export default{
		data: () => ({
			data: [
				{ 
					img: '1.png', 
					title: 'Mudah digunakan', 
					desc: 'Ease in monitoring startup' 
				},
				{ 
					img: '2.png', 
					title: 'Terkontrol', 
					desc: 'Ease in monitoring startup' 
				},
				{ 
					img: '3.png', 
					title: 'Informatif', 
					desc: 'Ease in monitoring startup' 
				},
				{ 
					img: '1.png', 
					title: 'Mudah dimengerti', 
					desc: 'Ease in monitoring startup' 
				}
			],
			geser: 0,
			clicked: 0
		}),
		methods: {
			changeSlide(val) {
				let hasil = this.data.length - 3				
				if (val === 'left') {
					if (this.clicked < hasil) {
						this.geser = this.geser - 33.333
					}

					// let first = this.data[this.clicked]
					// this.data.push(first)
					// setTimeout(() => {						
					// 	this.data.shift()						
					// }, 300)
					this.clicked++					
				} else {
					if (this.clicked > 0) {
						this.geser = this.geser + 33.333
					}		
					// let panjang = this.data.length-1
					// let last = this.data[panjang-this.clicked]
					// if (this.clicked > 0) this.data.unshift(last)					
					// setTimeout(() => {						
					// }, 300)	
					this.clicked--				
				}
			}
		}
	}
</script>
