import {
	getDataStartup,
	getShowStartup,
	postDataStartup
} from '@/config/startup/startup'

const dataStartup = {
	namespaced: true,
	state: {
		dataStartup: [],
		isLoading: false,
		status: null
	},
	mutations: {
		updateDataStartup(state, payload) {
			state.dataStartup = payload
		},
		updateStatus(state, payload) {
			state.status = payload
		},
		updateIsLoading(state, payload) {
			state.isLoading = payload
		}
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataStartup(context) {
			getDataStartup()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataStartup', {...res.data.data[0]})
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		getShowStartup(context) {
			getShowStartup()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataStartup', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataStartup(context, payload) {
			postDataStartup(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataStartup')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataStartup