import {
	getDataFounder,
	postDataFounder,
	putDataFounder,
	deleteDataFounder
} from '@/config/startup/founder'

const dataFounder = {
	namespaced: true,
	state: {
		dataFounder: [],
		isLoading: false,
		status: null
	},
	mutations: {
		updateDataFounder(state, payload) {
			state.dataFounder = payload
		},
		updateStatus(state, payload) {
			state.status = payload
		},
		updateIsLoading(state, payload) {
			state.isLoading = payload
		}
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataFounder(context) {
			getDataFounder()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataFounder', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataFounder(context, payload) {
			postDataFounder(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataFounder')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataFounder(context, payload) {
			// console.log(payload.id)
			const formdata = payload
			putDataFounder(formdata.get('id'), payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataFounder')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataFounder(context, payload) {
			deleteDataFounder(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataFounder')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataFounder