import {
	getDataSprint,
	postDataSprint,
	putDataSprint,
	deleteDataSprint,

	getDataList,
	moveDataList,
	postDataList,
	putDataList,
	deleteDataList,

	getDataCard,
	moveDataCard,
	moveDataCardList,
	postDataCard,
	putDataCard
} from '@/config/incubator/sprint'

const dataSprint = {
	namespaced: true,
	state: {
		dataSprint: [],
		dataList: [],
		dataCard: [],

		isLoading: false,
		isLoadingList: false,
		isLoadingCard: false,

		status: null,
		statusList: null,		
		statusCard: null
	},
	mutations: {
		updateDataSprint(state, payload) {
			state.dataSprint = payload
		},
		updateDataList(state, payload) {
			state.dataList = payload
		},
		updateDataCard(state, payload) {
			state.dataCard = payload
		},

		updateStatus(state, payload) {
			state.status = payload
		},
		updateStatusList(state, payload) {
			state.statusList = payload
		},
		updateStatusCard(state, payload) {
			state.statusCard = payload
		},

		updateIsLoading(state, payload) {
			state.isLoading = payload
		},
		updateIsLoadingList(state, payload) {
			state.isLoadingList = payload
		},
		updateIsLoadingCard(state, payload) {
			state.isLoadingCard = payload
		}		
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},		
		updateIsLoadingList(context, payload) {
			context.commit('updateIsLoadingList', payload)
		},
		updateIsLoadingCard(context, payload) {
			context.commit('updateIsLoadingCard', payload)
		},

		getDataSprint(context, payload) {
			getDataSprint(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataSprint', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataSprint(context, payload) {
			postDataSprint(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataSprint', payload.startup_id)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataSprint(context, payload) {
			putDataSprint(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataSprint', payload.startup_id)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataSprint(context, payload) {
			deleteDataSprint(payload.id)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataSprint', payload.startup_id)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},

		// list
		getDataList(context, payload) {
			getDataList(payload)
				.then(res => {
					if (res.status === 200) {
						let data = []
						res.data.data.map(list => {
							data[parseInt(list.urut)-1] = {
								type: 'draggable',
								id: list.id,
								name: list.nama,
								props: {
									orientation: 'vertical'
								},
								children: []
							}
							list.card.map(card => {
								data[parseInt(list.urut)-1].children[card.urut-1] = {
									type: 'draggable',
									id: card.id,
									data: card.title,
									file: card.berkas,
									urut: card.urut
								}
							})
						})
						context.commit('updateDataList', data)
						context.commit('updateStatusList', true)
						context.dispatch('updateIsLoadingList', false)
					}
				})
				.catch(() => {
					context.commit('updateStatusList', false)					
					context.dispatch('updateIsLoadingList', false)
				})
		},
		postDataList(context, payload) {
			postDataList(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataList', payload.board_id)
						context.commit('updateStatusList', true)
						context.dispatch('updateIsLoadingList', false)
					}
				})
				.catch(() => {
					context.commit('updateStatusList', false)					
					context.dispatch('updateIsLoadingList', false)
				})
		},
		putDataList(context, payload) {
			putDataList(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataList', payload.board_id)
						context.commit('updateStatusList', true)
						context.dispatch('updateIsLoadingList', false)
					}
				})
				.catch(() => {
					context.commit('updateStatusList', false)					
					context.dispatch('updateIsLoadingList', false)
				})
		},
		moveDataList(context, payload) {
			moveDataList(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataList', payload.board_id)						
					}
				})
		},
		deleteDataList(context, payload) {
			deleteDataList(payload.id)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataList', payload.board_id)
						context.commit('updateStatusList', true)
						context.dispatch('updateIsLoadingList', false)
					}
				})
				.catch(() => {
					context.commit('updateStatusList', false)					
					context.dispatch('updateIsLoadingList', false)
				})
		},

		// card
		getDataCard(context, payload) {
			getDataCard(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataCard')
						context.commit('updateStatusCard', true)
						context.dispatch('updateIsLoadingCard', false)
					}
				})
				.catch(() => {
					context.commit('updateStatusCard', false)					
					context.dispatch('updateIsLoadingCard', false)
				})
		},
		moveDataCard(context, payload) {
			moveDataCard(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataList', payload.board_id)						
					}
				})
		},
		moveDataCardList(context, payload) {
			const board_id = payload.board_id
			delete payload.board_id
			moveDataCardList(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataList', board_id)						
					}
				})
		},
		postDataCard(context, payload) {
			const board_id = payload.board_id
			delete payload.board_id
			postDataCard(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataList', board_id)
						context.commit('updateStatusCard', true)
						context.dispatch('updateIsLoadingCard', false)
					}
				})
				.catch(() => {
					context.commit('updateStatusCard', false)					
					context.dispatch('updateIsLoadingCard', false)
				})
		},
		putDataCard(context, payload) {
			putDataCard(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataCard', payload.list_id)
						context.commit('updateStatusCard', true)
						context.dispatch('updateIsLoadingCard', false)
					}
				})
				.catch(() => {
					context.commit('updateStatusCard', false)					
					context.dispatch('updateIsLoadingCard', false)
				})
		},
	}
}

export default dataSprint