import http from '../http'

export function getDataTenant() {
	return http.get('incubator/tenan')
}

export function postDataTenant(data) {
	return http.post('incubator/tenan', data)
}

export function putDataTenant(id, data) {
	return http.post('incubator/tenan/update/'+id, data)
}

export function deleteDataTenant(data) {
	return http.delete('incubator/tenan/'+data)
}

export function getDataKategoriTenant() {
	return http.get('incubator/kategori-tenan')
}

export function postDataKategoriTenant(data) {
	return http.post('incubator/kategori-tenan', data)
}

export function deleteDataKategoriTenant(data) {
	return http.delete('incubator/kategori-tenan/'+data)
}