import {
	getDataProduct,
	postDataProduct,
	putDataProduct,
	deleteDataProduct
} from '@/config/startup/product'

const dataProduct = {
	namespaced: true,
	state: {
		dataProduct: [],
		isLoading: false,
		status: null
	},
	mutations: {
		updateDataProduct(state, payload) {
			state.dataProduct = payload
		},
		updateStatus(state, payload) {
			state.status = payload
		},
		updateIsLoading(state, payload) {
			state.isLoading = payload
		}
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataProduct(context) {
			getDataProduct()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataProduct', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataProduct(context, payload) {
			postDataProduct(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataProduct')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataProduct(context, payload) {
			// console.log(payload.id)
			const formdata = payload
			putDataProduct(formdata.get('id'), payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataProduct')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataProduct(context, payload) {
			deleteDataProduct(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataProduct')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataProduct