import Vue from 'vue'
import Vuetify, {
	// app started
	VApp, VNavigationDrawer,
	VAppBar, VContent, VFooter,

	// list
	VList, VListItem, VListItemIcon, 
	VListItemContent, VListItemTitle, VListItemGroup,
	VListItemAvatar, VListItemAction, VListGroup,	

	// grid system
	VRow, VCol, 

	// card
	VCard, VCardTitle, VCardText,
	VSpacer, VCardActions, 

	// form
	VForm, VSelect, VTextField, VFileInput, 
	VRadio, VRadioGroup, VTextarea, VAutocomplete,
	VSwitch, VCheckbox,

	// tabs
	VTabs, VTab, VTabsItems, VTabItem,

	// dialog
	VDialog,

	// date picker
	VDatePicker,

	// extra
	VAvatar, VBtn, VIcon, 
	VAppBarNavIcon, VDivider,
	VDataTable, VAlert, VImg, VSheet,
	VSkeletonLoader, VSnackbar, VBadge	
} from 'vuetify/lib'
import { Ripple } from 'vuetify/lib/directives'
import '@mdi/font/css/materialdesignicons.css'
// import 'vuetify/dist/vuetify.min.css'

Vue.use(Vuetify, {
	components: {
		// app started
		VApp, VNavigationDrawer,
		VAppBar, VContent, VFooter,

		// list
		VList, VListItem, VListItemIcon, 
		VListItemContent, VListItemTitle, VListItemGroup,
		VListItemAvatar, VListItemAction, VListGroup,	

		// grid system
		VRow, VCol, 

		// card
		VCard, VCardTitle, VCardText,
		VSpacer, VCardActions, 

		// form
		VForm, VSelect, VTextField, VFileInput, 
		VRadio, VRadioGroup, VTextarea, VAutocomplete,
		VSwitch, VCheckbox,

		// tabs
		VTabs, VTab, VTabsItems, VTabItem,

		// dialog
		VDialog,

		// date picker
		VDatePicker,

		// extra
		VAvatar, VBtn, VIcon, 
		VAppBarNavIcon, VDivider,
		VDataTable, VAlert, VImg, VSheet,
		VSkeletonLoader, VSnackbar, VBadge
	},
  directives: {
    Ripple
  }
})

const opts = {
	icons: {
    iconfont: 'mdi',
  }
}

export default new Vuetify(opts)