import http from '../http.js'

export function getDataPaket(id) {
  return http.get('admin/incubator/paket/'+id)
}

export function putDataPaket(id) {
  return http.post('admin/incubator/edit-paket/'+id)
}

export function putAktifasiPaket(data) {
  return http.post('admin/incubator/aktifkan-paket', data)
}

export function cekPaketExpired() {
  return http.post('admin/incubator/cek-expired/')
}

export function postAktifasiIncubator(data) {
	return http.post('admin/incubator/enable', data)
}