<template>
	<div class="fourth mt-lg-12">
		<div class="container">
			<div class="d-flex align-center carousel">
				<v-btn
					fab depressed
					small
					@click="changeSlide('left')"
					class="carousel-nav purple-abp"					
					dark>
					<v-icon>mdi-arrow-left</v-icon>
				</v-btn>
				<div 
					v-for="item in 5" :key="item"
					class="slide fade"
					:class="{'active': slideActive===item?true:false}">
					<img
						alt="alt"
						src="../../assets/slide.png"
						width="100%"
					/>
				</div>
				<v-btn
					fab depressed
					small
					@click="changeSlide('right')"
					class="carousel-nav purple-abp"
					dark>
					<v-icon>mdi-arrow-right</v-icon>
				</v-btn>
			</div>
			<div class="mt-3">
				<center>
					<span 
						v-for="item in 5" :key="item"
						class="dot"
						:class="{'active': slideActive===item?true:false}"
						@click="selectSlide(item)"
					></span>
				</center>				
			</div>
		</div>
	</div>
</template>

<style scoped>
	.slide{
		margin: 0 16px;
		width: 100%;
		display: none;
	}
	.slide.active{ display: block; }
	.carousel{
		margin-right: -56px;
		margin-left: -56px;
	}
	.carousel-nav{
		z-index: 1;
		/*position: absolute;*/
	}
	@media screen and (max-width: 500px){
		.carousel{ margin: 0; }
		.slide{ margin: 0 -20px; }
	}
</style>

<script>
	export default{
		data: () => ({
			slideActive: 1
		}),
		methods: {
			selectSlide(val) {
				this.slideActive = val
			},
			changeSlide(val) {
				if (val === 'left') {
					if (this.slideActive === 1) {
						this.slideActive = 5
					} else {
						this.slideActive--
					}
				} else {
					if (this.slideActive === 5) {
						this.slideActive = 1
					} else {
						this.slideActive++
					}
				}
			}
		}
	}
</script>
