import http from '../http'

export function getDataSprint() {
	return http.get('startup/sprint')
}

export function postDataSprint(data) {
	return http.post('startup/sprint',data)
}

export function putDataSprint(id, data) {
	return http.post('startup/sprint/update/'+id, data)
}

export function deleteDataSprint(data) {
	return http.delete('startup/sprint/'+data)
}

export function getDataCard(data) {
	return http.get('startup/card/'+data)
}

export function getDetailCard(data) {
	return http.get('startup/card/show/'+data)
}

export function moveDataCard(data) {
	return http.get(`startup/card/move/card/${data.card_pindah_id}/${data.card_kepindah_id}`)
}

export function moveDataCardList(data) {
	return http.post(`startup/card/move/tolist`, data)
}

export function copyDataCard(data) {
	return http.get(`startup/card/copy/list/${data.card_id}/${data.list_id}`)
}

export function moveDataCardSprintList(data) {
	return http.get(`startup/card/move/list/${data.card_id}/${data.list_id}/${data.column_id}`)
}

export function postDataCard(data) {
	return http.post('startup/card',data)
}

export function deleteDataCard(data) {
	return http.delete('startup/card/'+data.card_id+'/'+data.column_id)
}

export function putDataCard(id, data) {
	return http.post('startup/card/update/'+id, data)
}

export function putDataCardName(id, data) {
	return http.post('startup/card/update/nama/'+id, data)
}

export function putDataCardDesc(id, data) {
	return http.post('startup/card/update/description/'+id, data)
}

export function putDataCardFile(id, data) {
	return http.post('startup/card/update/berkas/'+id, data)
}

export function getDataList(data) {
	return http.get('startup/sprint-list/'+data)
}

export function moveDataList(data) {
	return http.get(`startup/sprint-list/move/list/${data.list_pindah}/${data.list_kepindah}`)
}

export function postDataList(data) {
	return http.post('startup/sprint-list',data)
}

export function putDataList(id, data) {
	return http.post('startup/sprint-list/update/'+id, data)
}

export function deleteDataList(data) {
	return http.delete('startup/sprint-list/'+data)
}