<template>
	<v-app v-if="modalToken">
		<v-overlay :value="modalToken">
			<v-card max-width="400px" color="white" light>
				<v-card-text class="title">
					Sesi telah berakhir, silahkan login kembali
				</v-card-text>
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn color="primary" text @click="oke">
						oke
					</v-btn>
					<v-btn color="primary" depressed dark @click="login">Login</v-btn>
				</v-card-actions>
			</v-card>
		</v-overlay>
	</v-app>
</template>

<script>
	import { mapState } from 'vuex'

	export default {
		methods: {
			login () {
				this.$store.dispatch('updateModalToken', false)
				window.localStorage.removeItem('token_abp')
				window.location.href = '/'
			},
			oke() {
				this.$store.dispatch('updateModalToken', false)				
			}
		},	

		computed: {
			...mapState(['modalToken'])
		}	
	}
</script>