import {
	getDataMentor,
	postDataMentor,
	putDataMentor,
	deleteDataMentor
} from '@/config/incubator/mentor'

const dataMentor = {
	namespaced: true,
	state: {
		dataMentor: [],
		isLoading: false,
		status: null
	},
	mutations: {
		updateDataMentor(state, payload) {
			state.dataMentor = payload
		},
		updateStatus(state, payload) {
			state.status = payload
		},
		updateIsLoading(state, payload) {
			state.isLoading = payload
		}
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataMentor(context) {
			getDataMentor()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataMentor', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataMentor(context, payload) {
			postDataMentor(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataMentor')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataMentor(context, payload) {
			const formdata = payload
			putDataMentor(formdata.get('id'), payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataMentor')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataMentor(context, payload) {
			deleteDataMentor(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataMentor')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataMentor