import http from '../http'

export function getDataSprint(data) {
	return http.get('incubator/sprint/'+data)
}

export function postDataSprint(data) {
	return http.post('incubator/sprint',data)
}

export function putDataSprint(id, data) {
	return http.post('incubator/sprint/update/'+id, data)
}

export function deleteDataSprint(data) {
	return http.delete('incubator/sprint/'+data)
}

// list
export function getDataList(data) {
	return http.get('incubator/sprint-list/'+data)
}

export function moveDataList(data) {
	return http.get(`incubator/sprint-list/move/list/${data.list_pindah}/${data.list_kepindah}`)
}

export function postDataList(data) {
	return http.post('incubator/sprint-list',data)
}

export function putDataList(id, data) {
	return http.post('incubator/sprint-list/update/'+id, data)
}

export function deleteDataList(data) {
	return http.delete('incubator/sprint-list/'+data)
}

// card
export function getDataCard(data) {
	return http.get('incubator/card/'+data)
}

export function getDetailCard(data) {
	return http.get('incubator/card/show/'+data)
}

export function moveDataCard(data) {
	return http.get(`incubator/card/move/card/${data.card_pindah_id}/${data.card_kepindah_id}`)
}

export function moveDataCardList(data) {
	return http.post(`incubator/card/move/tolist`, data)
}

export function copyDataCard(data) {
	return http.get(`incubator/card/copy/list/${data.card_id}/${data.list_id}/${data.startup_id}`)
}

export function moveDataCardSprintList(data) {
	return http.get(`incubator/card/move/list/${data.card_id}/${data.list_id}/${data.column_id}`)
}

export function postDataCard(data) {
	return http.post('incubator/card',data)
}

export function deleteDataCard(data) {
	return http.delete('incubator/card/'+data.card_id+'/'+data.column_id)
}

export function putDataCard(id, data) {
	return http.post('incubator/card/update/'+id, data)
}

export function putDataCardName(id, data) {
	return http.post('incubator/card/update/nama/'+id, data)
}

export function putDataCardDesc(id, data) {
	return http.post('incubator/card/update/description/'+id, data)
}

export function putDataCardFile(id, data) {
	return http.post('incubator/card/update/berkas/'+id, data)
}