import {
	getDataTractionResponse,
	getDetailTraction,
	deleteDataTraction,
	putDataTraction,
	postDataTraction,
	getDataTraction,
	postDataTractionShow
} from '@/config/incubator/traction'

const dataTraction = {
	namespaced: true,
	state: {
		dataTraction: [],
		dataStartup: [],
		detailTraction: {},
		dataResponse: [],
		isLoading: false,
		status: null
	},
	mutations: {
		updateDetailTraction(state, payload) {
			state.detailTraction = payload
		},
		updateDataTraction(state, payload) {
			state.dataTraction = payload
		},
		updateDataResponse(state, payload) {
			state.dataResponse = payload
		},
		updateDataStartup(state, payload) {
			state.dataStartup = payload
		},
		updateStatus(state, payload) {
			state.status = payload
		},
		updateIsLoading(state, payload) {
			state.isLoading = payload
		}
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		postDataTractionShow(context, payload) {
			postDataTractionShow(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataResponse', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataTraction(context, payload) {
			getDataTraction(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataTraction', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataTractionResponse(context, payload) {
			getDataTractionResponse(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDetailTraction', res.data.data[0])
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		getDetailTraction(context, payload) {
			getDetailTraction(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataStartup', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataTraction(context, payload) {
			postDataTraction(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataTraction')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataTraction(context, payload) {
			putDataTraction(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataTraction')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataTraction(context, payload) {
			deleteDataTraction(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataTraction')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataTraction