import http from '../http'

export function getDataAward() {
	return http.get('incubator/award')
}

export function postDataAward(data) {
	return http.post('incubator/award',data)
}

export function putDataAward(id, data) {
	// console.log(data)
	return http.post('incubator/award/update/'+id, data)
}

export function deleteDataAward(data) {
	return http.delete('incubator/award/'+data)
}