import {
	getDataTenant,
	postDataTenant,
	putDataTenant,
	deleteDataTenant,
	getDataKategoriTenant,
	postDataKategoriTenant,
	deleteDataKategoriTenant
} from '@/config/incubator/tenant'

const dataTenant = {
	namespaced: true,
	state: {
		dataTenant: [],
		dataKategori: [],
		isLoading: false,
		status: null
	},
	mutations: {
		updateDataTenant(state, payload) {
			state.dataTenant = payload
		},
		updateDataKategori(state, payload) {
			state.dataKategori = payload
		},
		updateStatus(state, payload) {
			state.status = payload
		},
		updateIsLoading(state, payload) {
			state.isLoading = payload
		}
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataTenant(context) {
			getDataTenant()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataTenant', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataKategoriTenant(context) {
			getDataKategoriTenant()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataKategori', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataTenant(context, payload) {
			postDataTenant(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataTenant')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataTenant(context, payload) {
			const formdata = payload
			putDataTenant(formdata.get('id'), payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataTenant')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataTenant(context, payload) {
			deleteDataTenant(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataTenant')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataKategoriTenant(context, payload) {
			postDataKategoriTenant(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataKategoriTenant')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataKategoriTenant(context, payload) {
			deleteDataKategoriTenant(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataKategoriTenant')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataTenant