import {
	getDataPaket,
	putDataPaket,
  putAktifasiPaket,
	cekPaketExpired,
	postAktifasiIncubator
} from '@/config/admin/paket'

const dataPaket = {
	namespaced: true,
	state: {
		dataPaket: [],
		isLoading: false,
		isLoadingAktifasi: false,
		status: null
	},
	mutations: {
		updateDataPaket(state, payload) {
			state.dataPaket = payload
		},
		updateStatus(state, payload) {
			state.status = payload
		},
		updateIsLoading(state, payload) {
			state.isLoading = payload
		},
		updateIsLoadingAktifasi(state, payload) {
			state.isLoadingAktifasi = payload
		}
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		updateIsLoadingAktifasi(context, payload) {
			context.commit('updateIsLoadingAktifasi', payload)
		},
		getDataPaket(context, payload) {
			getDataPaket(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataPaket', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
    postAktifasiIncubator(context, payload) {
			const id_paket = payload.id_paket
			delete payload.id_paket
      postAktifasiIncubator(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataPaket', id_paket)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoadingAktifasi', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoadingAktifasi', false)
				})
		},
    putAktifasiPaket(context, payload) {
			const id_paket = payload.id_paket
			delete payload.id_paket
      putAktifasiPaket(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataPaket', id_paket)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoadingAktifasi', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoadingAktifasi', false)
				})
		},
		putDataPaket(context, payload) {
			const id_paket = payload.id_paket
			delete payload.id_paket
			putDataPaket(payload.id_incubator)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataPaket', id_paket)						
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)						
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		cekPaketExpired(context) {
			cekPaketExpired()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataPaket', res.data.data.total)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)						
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataPaket