<template>
	<div class="fifth mt-12" id="features">
		<div class="container">
			<p class="display-2 font-weight-bold">
				Fitur Kami
			</p>
			<v-card flat color="blue lighten-5">
				<v-card-title>
					<div class="purple--text">
						Berbagai fitur di dalam satu aplikasi
					</div>
					<v-spacer></v-spacer>
					<v-btn
						fab depressed
						small
						class="mr-3 white--text purple-abp"						
						:disabled="clicked < data.length-3?false:true"
						@click="changeSlide('left')">
						<v-icon>mdi-arrow-left</v-icon>
					</v-btn>
					<v-btn
						fab depressed
						small
						:disabled="clicked > 0?false:true"
						class="white--text purple-abp"
						@click="changeSlide('right')">
						<v-icon>mdi-arrow-right</v-icon>
					</v-btn>
				</v-card-title>
				<v-card-text style="overflow: hidden;">
					<v-row
						:style="{
							'transform': `translateX(${geser}%)`,
							'transition': 'transform 0.3s ease',
							'flex-wrap': 'nowrap'
						}">
						<v-col 
							cols="12" md="4" sm="12"
							v-for="(item, i) in data" :key="i">
							<v-sheet class="py-6">
								<center>
									<v-img
										:src="require(`../../assets/features/${item.img}`)"
										width="100px"
										alt="feature"
									></v-img>
									<div class="mt-3">
										<div class="headline black--text font-weight-bold">
											{{ item.title }}
										</div>
										<div>{{ item.desc }}</div>
									</div>
								</center>
							</v-sheet>
						</v-col>
					</v-row>
				</v-card-text>
			</v-card>
		</div>
	</div>
</template>

<style scoped>
	
</style>

<script>
	export default{
		data: () => ({
			data: [
				{ 
					img: 'weapons.png', 
					title: 'Easy to use', 
					desc: 'Ease in monitoring startup' 
				},
				{ 
					img: 'video.png', 
					title: 'Controlled', 
					desc: 'Ease in monitoring startup' 
				},
				{ 
					img: 'financial.png', 
					title: 'Informatif', 
					desc: 'Ease in monitoring startup' 
				},
				{ 
					img: 'weapons.png', 
					title: 'Fast', 
					desc: 'Ease in monitoring startup' 
				}
			],			
			geser: 0,
			clicked: 0
		}),
		methods: {
			changeSlide(val) {
				let hasil = this.data.length - 3				
				if (val === 'left') {
					if (this.clicked < hasil) {
						this.geser = this.geser - 33.333
					}

					// let first = this.data[this.clicked]
					// this.data.push(first)
					// setTimeout(() => {						
					// 	this.data.shift()						
					// }, 300)
					this.clicked++					
				} else {
					if (this.clicked > 0) {
						this.geser = this.geser + 33.333
					}		
					// let panjang = this.data.length-1
					// let last = this.data[panjang-this.clicked]
					// if (this.clicked > 0) this.data.unshift(last)					
					// setTimeout(() => {						
					// }, 300)	
					this.clicked--				
				}
			}
		}
	}
</script>
