import http from '../http'

export function getDataProduct() {
	return http.get('startup/product')
}

export function postDataProduct(data) {
	return http.post('startup/product',data)
}

export function putDataProduct(id, data) {
	// console.log(data)
	return http.post('startup/product/update/'+id, data)
}

export function deleteDataProduct(data) {
	return http.delete('startup/product/'+data)
}