<template>
	<div class="seventh mt-12" id="testimonial">
		<div class="text-center mb-6">
			<div class="display-2 font-weight-bold mb-2">
				Pendapat Mereka Tentang Kami
			</div>
			<div>
				Berikut pendapat incubator dan startup terhadap sistem kami.
			</div>
		</div>
		<div class="container">
			<div class="d-flex align-center carousel">
				<v-btn
					fab depressed
					small
					@click="changeSlide('left')"
					class="carousel-nav purple-abp"
					dark>
					<v-icon>mdi-arrow-left</v-icon>
				</v-btn>
				<div 
					v-for="item in 5" :key="item"
					class="slide fade w-100"
					:class="{'active': slideActive===item?true:false}">
					<v-sheet class="d-flex blue lighten-5 align-center">
						<img 
							src="@/assets/testimoni.png" 
							alt="testimoni"
							class="d-none d-sm-flex" 
						/>
						<div class="pa-6">
							<p class="display-1 text-center">
								“Fitur yang di sediakan pada platoform ini sangat bermanfaat untuk digunakan dalam hal monitoring progress startup semua kinerja menjadi terukur dan tersusun”
							</p>
							<div class="d-flex justify-end">
								<div class="text-center">
									<b class="headline">Renald Khasali,</b>
									<div class="grey--text">
										Directur Incubator Indonesia
									</div>
								</div>
							</div>
						</div>
					</v-sheet>
				</div>
				<v-btn
					fab depressed
					small
					@click="changeSlide('right')"
					class="carousel-nav purple-abp"
					dark>
					<v-icon>mdi-arrow-right</v-icon>
				</v-btn>
			</div>
			<div class="mt-3">
				<center>
					<span 
						v-for="item in 5" :key="item"
						class="dot"
						:class="{'active': slideActive===item?true:false}"
						@click="selectSlide(item)"
					></span>
				</center>				
			</div>
		</div>
	</div>
</template>

<style scoped>
	.slide{
		margin: 0 16px;
		width: 100%;
		display: none;
	}
	.slide.active{ display: block; }
	.w-100{ width: 100%; }
	.relative{ position: relative; }
	.jabatan{
		position: absolute;
		bottom: 16px;
		right: 32px;
		text-align: center;
	}
	.carousel{
		margin-right: -56px;
		margin-left: -56px;
	}
	.carousel-nav{ z-index: 1; }
	@media screen and (max-width: 500px){
		.carousel{ margin: 0; }
		.slide{ margin: 0 -20px; }
		.jabatan{ position: static; }
		/*.v-appalication .display-1{ font-size: 20px; }*/
	}
</style>

<script>
	export default{
		data: () => ({
			slideActive: 1
		}),
		methods: {
			selectSlide(val) {
				this.slideActive = val
			},
			changeSlide(val) {
				if (val === 'left') {
					if (this.slideActive === 1) {
						this.slideActive = 5
					} else {
						this.slideActive--
					}
				} else {
					if (this.slideActive === 5) {
						this.slideActive = 1
					} else {
						this.slideActive++
					}
				}
			}
		}
	}
</script>
