import http from '../http'

export function getDataAward() {
	return http.get('startup/award')
}

export function postDataAward(data) {
	return http.post('startup/award',data)
}

export function putDataAward(id, data) {
	// console.log(data)
	return http.post('startup/award/update/'+id, data)
}

export function deleteDataAward(data) {
	return http.delete('startup/award/'+data)
}