import {
	postLoginAdmin,
	postLoginIncubator,
	postLoginStartup
} from '@/config/login'

const dataLogin = {
	namespaced: true,
	state: {
		status: null,
		isLoading: false
	},
	mutations: {
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		},
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		postLoginAdmin(context, payload) {
			postLoginAdmin(payload)
				.then(res => {
					if (res.status === 200) {
						window.localStorage.setItem('token_abp', res.data.data.access_token)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
						window.location.href = '/admin'
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postLoginIncubator(context, payload) {
			postLoginIncubator(payload)
				.then(res => {
					if (res.status === 200) {
						window.localStorage.setItem('token_abp', res.data.data.access_token)
						window.localStorage.setItem('aktifasi_incubator', res.data.data.aktifasi)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
						window.location.href = '/incubator'
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postLoginStartup(context, payload) {
			postLoginStartup(payload)
				.then(res => {
					if (res.status === 200) {
						window.localStorage.setItem('token_abp', res.data.data.access_token)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
						window.location.href = '/startup'
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataLogin