import {
	getDataIncubator,
	postDataIncubator
} from '@/config/incubator/incubator'

const dataIncubator = {
	namespaced: true,
	state: {
		dataIncubator: [],
		isLoading: false,
		status: null
	},
	mutations: {
		updateDataIncubator(state, payload) {
			state.dataIncubator = payload
		},
		updateStatus(state, payload) {
			state.status = payload
		},
		updateIsLoading(state, payload) {
			state.isLoading = payload
		}
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataIncubator(context) {
			getDataIncubator()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataIncubator', {...res.data.data[0]})
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataIncubator(context, payload) {
			postDataIncubator(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataIncubator')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataIncubator