import Vue from 'vue'
import VueRouter from 'vue-router'
import LandingPage from '../views/LandingPage.vue'

Vue.use(VueRouter)

// router guard aktifasi
const isAktifasi = window.localStorage.getItem('aktifasi_incubator')
const aktifasiGuard = (to, from, next) => {
  if (isAktifasi === '1') {
    next()
  } else {
    next({ name: 'riwayat-pemesanan' })
  }
}

const routes = [
  {
    path: '/',
    name: 'landing-page',
    component: LandingPage
  },
  {
    path: '/login-admin',
    name: 'login-admin',
    component: () => import('../views/Admin/Login.vue')
  },
  {
    path: '/halaman-cetak-startup/:sprint_id',
    name: 'halaman-cetak-startup',
    component: () => import('../views/Startup/HalamanExportPdf.vue'),
    props: true
  },
  {
    path: '/login-startup',
    component: () => import('../views/Login.vue'),
    children: [
      {
        path: '/login-startup',
        name: 'login-startup',
        component: () => import('@/components/Startup/Login/Form.vue')        
      },
      {
        path: '/register-startup',
        name: 'register-startup',
        component: () => import('@/components/Startup/Register/Form.vue')        
      },
      {
        path: '/login-incubator',
        name: 'login-incubator',
        component: () => import('@/components/Incubator/Login/Form.vue')        
      },
      {
        path: '/register-incubator',
        name: 'register-incubator',
        component: () => import('@/components/Incubator/Register/Form.vue')        
      }
    ]
  },
  {
    path: '/admin',
    component: () => import('../views/Admin.vue'),
    meta: { requiresAuth: true },
    children: [
      {
        path: '/admin',
        name: 'admin',
        component: () => import('@/views/Admin/Dashboard.vue'),        
      },
      {
        path: '/daftar-incubator',
        name: 'daftar-incubator',
        component: () => import('@/views/Admin/DaftarIncubator.vue'),
      },
      {
        path: '/daftar-startup',
        name: 'daftar-startup',
        component: () => import('@/views/Admin/DaftarStartup.vue'),
      },
      {
        path: '/data-bank',
        name: 'data-bank',
        component: () => import('@/views/Admin/Bank.vue'),
      },
    ]
  },
  {
    path: '/incubator',
    component: () => import('../views/Incubator.vue'),
    meta: { requiresAuth: true },
    children: [
      {
        path: '/incubator',
        name: 'incubator',
        component: () => import('@/views/Incubator/Dashboard.vue'),        
        beforeEnter: aktifasiGuard,
      },
      {
        path: '/pemberitahuan',
        name: 'pemberitahuan',
        component: () => import('@/views/Incubator/Pemberitahuan.vue'),        
        beforeEnter: aktifasiGuard,
      },
      {
        path: '/riwayat-pemesanan',
        name: 'riwayat-pemesanan',
        component: () => import('@/views/Incubator/Riwayat.vue'),
      },
      {
        path: '/profil',
        component: () => import('@/views/Incubator/Profil.vue'),
        beforeEnter: aktifasiGuard,
        children: [
          {
            path: '/profil',
            name: 'profil',
            component: () => import('@/components/Incubator/Profil/Preview.vue'),
          },
          {
            path: '/edit-profil',
            name: 'edit-profil',
            component: () => import('@/components/Incubator/Profil/Edit.vue'),
          }
        ]        
      },
      {
        path: '/traction',
        component: () => import('@/views/Incubator/Traction.vue'),
        beforeEnter: aktifasiGuard,
        children: [
          {
            path: '/traction',
            name: 'traction',
            component: () => import('@/components/Incubator/Traction/DataTraction.vue'),
          },
          {
            path: '/create-traction',
            name: 'create-traction',
            component: () => import('@/components/Incubator/Traction/CreateTraction.vue'),
          },
          {
            path: '/edit-traction/:traction_id',
            name: 'edit-traction',
            component: () => import('@/components/Incubator/Traction/EditTraction.vue'),            
          },
          {
            path: '/startup-traction/:traction_id',
            name: 'startup-traction',
            props: true,
            component: () => import('@/components/Incubator/Traction/DataStartup.vue'),            
          },
          {
            path: '/show-response/:traction_id/:startup_id',
            name: 'show-response',
            props: true,
            component: () => import('@/components/Incubator/Traction/DataResponse.vue'),            
          }
        ]       
      },
      {
        path: '/tenant',
        component: () => import('@/views/Incubator/Tenant.vue'),  
        beforeEnter: aktifasiGuard,
        children: [
          {
            path: '/tenant',
            name: 'tenant',
            component: () => import('@/components/Incubator/Tenant/DataStartup.vue'),
          },
          {
            path: '/progress-tenant/:sprint_id/:startup_id',
            name: 'progress-tenant',
            component: () => import('@/components/Incubator/Tenant/DataProgress.vue'),
            props: true
          },
          {
            path: '/daftar-sprint/:startup_id',
            name: 'daftar-sprint',
            component: () => import('@/components/Incubator/Tenant/DataSprint.vue'),
            props: true
          }
        ]      
      }
    ]
  },
  {
    path: '/startup',
    component: () => import('../views/Startup.vue'),
    meta: { requiresAuth: true },
    children: [
      {
        path: '/startup',
        name: 'startup',
        component: () => import('@/views/Startup/Dashboard.vue'),        
      },
      {
        path: '/profil-startup',
        component: () => import('@/views/Startup/Profil.vue'),
        children: [
          {
            path: '/profil-startup',
            name: 'profil-startup',
            component: () => import('@/components/Startup/Profil/Preview.vue'),
          },
          {
            path: '/edit-profil-startup',
            name: 'edit-profil-startup',
            component: () => import('@/components/Startup/Profil/Edit.vue'),
          }
        ]        
      },
      {
        path: '/traction-startup',
        component: () => import('@/views/Startup/Traction.vue'),
        children: [
          {
            path: '/traction-startup',
            name: 'traction-startup',
            component: () => import('@/components/Startup/Traction/DataTraction.vue'),
          },
          {
            path: '/view-traction-startup/:traction_id',
            name: 'view-traction-startup',
            component: () => import('@/components/Startup/Traction/DataResponse.vue'),
            props: true
          },
        ]
      },
      {
        path: '/progress-startup',
        component: () => import('@/views/Startup/Progress.vue'),
        children: [
          {
            path: '/progress-startup',
            name: 'progress-startup',
            component: () => import('@/components/Startup/Progress/DataSprint.vue'),
          },
          {
            path: '/data-progress-startup/:sprint_id',
            name: 'data-progress-startup',
            props: true,
            component: () => import('@/components/Startup/Progress/DataProgress.vue'),
          }
        ]
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some(route => route.meta.requiresAuth)
  const isLoggedIn = window.localStorage.getItem('token_abp')
  
  if (requiresAuth && !isLoggedIn){ 
    next({ name: 'login-'+to.name })
  }else if(isLoggedIn && to.name === 'login-admin') {
    next({ name: 'admin' })
  }else if (isLoggedIn && to.name === 'login-startup') {
    next({ name: 'startup' })
  }else if (isLoggedIn && to.name === 'login-incubator') {
    next({ name: 'incubator' })
  }else if (requiresAuth && isLoggedIn) {
    next()
  }else {
    next()
  }
})

export default router
