import http from '../http.js'

export function getDataTraction() {
	return http.get('startup/traction')
}

export function getDetailTraction(id) {
	return http.get('startup/traction/view/'+id)
}

export function postDataTraction(data) {
	return http.post('startup/traction/submit', data)
}

export function postDataTractionShow(data) {
	return http.post('startup/traction/show', data)
}

export function getDataResponse(data) {
	return http.get(`startup/traction/view/response/${data}`)
}