import { 
  postDataPesan,
  postDataKonfirmasiPembayaran,
  getDataRiwayatPembayaran
} from "@/config/incubator/order.js";

const dataOrder = {
  namespaced: true,
	state: {
		dataRiwayat: [],
		isLoading: false,
		status: null
	},
	mutations: {
		updateDataRiwayat(state, payload) {
			state.dataRiwayat = payload
		},
		updateStatus(state, payload) {
			state.status = payload
		},
		updateIsLoading(state, payload) {
			state.isLoading = payload
		}
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataRiwayatPembayaran(context) {
			getDataRiwayatPembayaran()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataRiwayat', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataPesan(context, payload) {
			postDataPesan(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataRiwayatPembayaran')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataKonfirmasiPembayaran(context, payload) {
			postDataKonfirmasiPembayaran(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataRiwayatPembayaran')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
	}
}

export default dataOrder