import http from '../http'

export function getDataFounder() {
	return http.get('startup/founder')
}

export function postDataFounder(data) {
	return http.post('startup/founder',data)
}

export function putDataFounder(id, data) {
	// console.log(data)
	return http.post('startup/founder/update/'+id, data)
}

export function deleteDataFounder(data) {
	return http.delete('startup/founder/'+data)
}