import http from '../http'

export function postDataTraction(data) {
	return http.post('incubator/traction', data)
}

export function getDataTraction(data) {
	return http.get('incubator/traction', data)
}

export function putDataTraction(id, data) {
	return http.put('incubator/traction/'+id, data)
}

export function deleteDataTraction(id) {
	return http.delete('incubator/traction/'+id)
}

export function getDetailTraction(id) {
	return http.get('incubator/traction/view/'+id)
}

export function getDataTractionResponse(data) {
	return http.get(`incubator/traction/view/response/${data.traction_id}/${data.startup_id}`)
}

export function postDataTractionShow(data) {
	return http.post('incubator/traction/show', data)
}